import $ from 'jquery';
import * as firebase from 'firebase';
import { firebaseConf } from './appConfig';
import { authInit } from './auth';

firebase.initializeApp(firebaseConf);
const firebaseDB = firebase.database();
export const videoRef = firebaseDB.ref('videos');
export const quickRef = firebaseDB.ref('notes');
export default firebaseDB;

$(document).ready(() => {
	authInit();
});



