import $ from 'jquery';
import { videoRef, quickRef } from './firebaseSetup';
import speakInit from './speak';
import { playVideo } from './player';
import { isVideoNote } from './index';
import userObj from './auth';
import setSnackBar from './snackbar';
import vexDialog from './dialog';

import '../css/list.css';

const noteItem = `
	<div class="item" vid="_{{vid}}_">
		<div class="speakit" str="_{{spk}}_"></div>
		<div class="deleteit" str="_{{firebkey}}_"></div>
		<div class="jp">_{{jp}}_</div>
		<div class="zh">_{{zh}}_</div>
		_{{time}}_
		_{{note}}_
	</div>`;

const videoSec = `<div class="v-sec">_{{vid}}_</div>`;

const makeRuby = (str) => {
	const newStr = str.replace(/\n/g, '<br/>')
		.replace(/{/g, '<ruby><rb>')
		.replace(/\$/g, '</rb><rt>')
		.replace(/}/g, '</rt></ruby>');
	return newStr;
};

const makeJapanStr = (str) => {
	const newStr = str.replace(/{/g, '')
		.replace(/\$[^}]*}/g, '');
	return newStr;
};

const setListJS = () => {
	$('.speakit').off('click');
	$('.deleteit').off('click');
	$('.jp').off('click');

	$('.speakit').on('click', function () {
		const getJpTxt = $(this).attr('str');
		const getCleanTxt = makeJapanStr(getJpTxt);
		const newSpeaker = speakInit(getCleanTxt);
		window.speechSynthesis.speak(newSpeaker);
	});

	$('.jp').on('click', function () {
		if (isVideoNote()) {
			const getVid = $(this).parents('.item').attr('vid');
			const getTime = $(this).parents('.item').find('.time').text();
			playVideo(getVid, getTime);
		}
	});

	$('.deleteit').on('click', function () {
		const thisItem = $(this);

		if (userObj.user) {
			vexDialog.confirm({
				message: '',
				callback(value) {
					if (value) {
						const getDelId = thisItem.attr('str');
						if (isVideoNote()) {
							const getVid = thisItem.parents('.item').attr('vid');
							videoRef.child(`${getVid}/notes/${getDelId}`).remove();
							getVideoNote(); // eslint-disable-line
						} else {
							quickRef.child(getDelId).remove();
							getQuickNote(); // eslint-disable-line
						}
					} else {
						console.log('no');
					}
				},
			});
		} else {
			setSnackBar('');
		}
	});
};

export const getVideoNote = () => {
	videoRef.orderByChild('update').once('value').then((snapshot) => {
		if (snapshot) {
			const videoArr = [];
			snapshot.forEach((childSnapshot) => {
				const childKey = childSnapshot.key;
				const childData = childSnapshot.val();
				childData.vid = childKey;
				videoArr.unshift(childData);
			});

			if (videoArr) return videoArr;
		}
		return null;
	}).then((videoArr) => {
		$('#note-list').html('');
		$.each(videoArr, (i, j) => {
			const newVideoSec = videoSec.replace('_{{vid}}_', j.vid);
			$('#note-list').append(newVideoSec);

			if (j.notes) {
				$.each(j.notes, (ii, nobj) => {
					const thisJP = makeRuby(nobj.jp);
					const thisSpeak = makeJapanStr(nobj.jp);

					const newNoteItem = noteItem
						.replace('_{{vid}}_', j.vid)
						.replace('_{{zh}}_', nobj.zh)
						.replace('_{{jp}}_', thisJP)
						.replace('_{{spk}}_', thisSpeak)
						.replace('_{{firebkey}}_', ii)
						.replace('_{{note}}_', '')
						.replace('_{{time}}_', `<div class="time">${nobj.time}</div>`);
					$('#note-list').append(newNoteItem);
				});
			}
		});

		setListJS();
	})
	.catch((error) => {
		console.log(error);
	});
};

export const getQuickNote = () => {
	quickRef.once('value').then((snapshot) => {
		if (snapshot) {
			const quickArr = snapshot.val();

			if (quickArr) return quickArr;
		}
		return null;
	}).then((quickArr) => {
		$('#note-list').html('');
		$.each(quickArr, (i, nobj) => {
			const thisJP = makeRuby(nobj.jp);
			const thisSpeak = makeJapanStr(nobj.jp);

			const newNoteItem = noteItem
				.replace('_{{zh}}_', nobj.tw)
				.replace('_{{jp}}_', thisJP)
				.replace('_{{spk}}_', thisSpeak)
				.replace('_{{firebkey}}_', i)
				.replace('_{{time}}_', '')
				.replace('_{{note}}_', nobj.note ? `<div class="note">${nobj.note}</div>` : '');
			$('#note-list').append(newNoteItem);
		});

		setListJS();
	})
	.catch((error) => {
		console.log(error);
	});
};



