import $ from 'jquery';
import * as firebase from 'firebase';
import setSnackBar from './snackbar';
import vexDialog from './dialog';

const userObj = {};

const setUserJS = () => {
	if (userObj.user) {
		$('#signin-btn').addClass('hide');
		$('#signout-btn').removeClass('hide');
		$('.editor .form').removeClass('hide');
	} else {
		$('#signin-btn').removeClass('hide');
		$('#signout-btn').addClass('hide');
		$('.editor .form').addClass('hide');
	}
};

const clearUser = () => {
	userObj.user = null;
	setUserJS();
};

const signinUser = (email, password) => {
	firebase.auth().signInWithEmailAndPassword(email, password).catch((error) => {
		// const errorCode = error.code;
		const errorMessage = error.message;
		setSnackBar(errorMessage);
	});
};

const getCurrentUser = () => {
	const currentUser = firebase.auth().currentUser;
	if (currentUser) {
		// User is signed in.
		userObj.user = currentUser;
		userObj.data = currentUser.providerData;
		console.log(userObj.data[0]);
		setUserJS();
	} else {
		// No user is signed in.
	}
};

const detectUserChange = () => {
	firebase.auth().onAuthStateChanged((user) => {
		if (user) {
			// User is signed in.
			getCurrentUser();
		} else {
			// User is signed out.
			clearUser();
		}
	});
};

export const authInit = () => {
	console.log('authInit');
	detectUserChange();

	$('#signin-btn').on('click', () => {
		vexDialog.open({
			message: '',
			input: [
				'<input name="username" type="text" placeholder="Email" required />',
				'<input name="password" type="password" placeholder="Password" required />',
			].join(''),
			buttons: [
				$.extend({}, vexDialog.buttons.YES, { text: 'Login' }),
				$.extend({}, vexDialog.buttons.NO, { text: 'Back' }),
			],
			callback(data) {
				if (data) {
					signinUser(data.username, data.password);
				}
			},
		});
	});

	$('#signout-btn').on('click', () => {
		firebase.auth().signOut().then(() => {
			console.log('Sign-out successful');
			clearUser();
		}, (error) => {
			// An error happened.
			console.log(error);
		});
	});
};

export default userObj;



