import $ from 'jquery';
import jqGET from './index';

const playerObj = {};

const formatFloat = (num, pos) => {
	// const size = 10 ** pos;
	const size = Math.pow(10, pos); // eslint-disable-line
	return Math.round(num * size) / size;
};

const youtubeParser = (urlStr) => {
	const regExp = /^.*((youtu.be\/)|(v\/)|(\/u\/\w\/)|(embed\/)|(watch\?))\??v?=?([^#&?]*).*/;
	const match = urlStr.match(regExp);
	if (match && match[7].length === 11) {
		return match[7];
	}

	return '';
};

export const playVideo = (vid, time) => {
	playerObj.player.stopVideo();
	playerObj.player.loadVideoById(vid);

	if (time) {
		playerObj.player.seekTo(parseFloat(time));
	}
};

export const pauseVideo = () => {
	playerObj.player.pauseVideo();
};

export const getVideoTime = () => {
	const t = playerObj.player.getCurrentTime();
	let vtime = t <= 4 ? t : (parseInt((t * 10), 10) / 10) - 4;
	vtime = formatFloat(vtime, 2);
	return vtime;
};

export const getVideoID = () => {
	const id = playerObj.player.getVideoData().video_id;
	return id;
};

const onPlayerReady = () => {
	if (jqGET.vid) {
		playVideo(jqGET.vid);
	}

	$('.video-url').on('change', function () {
		const videoURL = $(this).val();
		if (videoURL) {
			const videoID = youtubeParser(videoURL);
			if (videoID) {
				playVideo(videoID);

				$(this).blur();
				$(this).addClass('updated');
			}
		}
	});

	$('.video-url').on('focus', function () {
		$(this).removeClass('updated');
	});
};

const onPlayerStateChange = () => {
	if (event.data === YT.PlayerState.PAUSED) { // eslint-disable-line
	} else if(event.data === YT.PlayerState.PLAYING){ // eslint-disable-line
	}
};

const onYouTubeIframeAPIReady = () => {
	setTimeout(() => {
		playerObj.player = new YT.Player('playing', { // eslint-disable-line
			width: '100%',
			height: '300',
			events: {
				onStateChange: onPlayerStateChange,
				onReady: onPlayerReady,
			},
		});
	}, 1000);
};

const playerInit = () => {
	if (typeof YT === 'undefined') { // eslint-disable-line
		window.onYouTubePlayerAPIReady = () => {
			onYouTubeIframeAPIReady();
		};
	} else {
		onYouTubeIframeAPIReady();
	}
};

export default playerInit;



