const speakInit = (str) => {
	const voices = window.speechSynthesis.getVoices();
	// ja-JP only these 2;
	// voices[31] better
	// voices[74] 
	const speaker = new SpeechSynthesisUtterance();
	speaker.lang = 'ja-JP';
	speaker.volume = 1.0;
	speaker.rate = 0.8;
	speaker.pitch = 1.0;
	speaker.voice = voices[31];
	speaker.text = str || '';
	return speaker;
};

export default speakInit;



