import $ from 'jquery';
import getQueryObj from './getQueryObj';
import { getVideoNote, getQuickNote } from './list';
import { setQuickNoteEditor, setVideoNoteEditor } from './editor';
import speakInit from './speak';

import '../css/index.css';
import '../library/font-awesome/css/font-awesome.css';

const jqGET = getQueryObj();

export const isVideoNote = () => !jqGET.t || jqGET.t === 'video';

$(document).ready(() => {
	if (jqGET.t && jqGET.t === 'quick') {
		getQuickNote();
		setQuickNoteEditor();
	} else {
		getVideoNote();
		setVideoNoteEditor();
	}
});

window.speechSynthesis.onvoiceschanged = function () {
	speakInit();
};

export default jqGET;



