import $ from 'jquery';
import { videoRef, quickRef } from './firebaseSetup';
import { getVideoNote, getQuickNote } from './list';
import { isVideoNote } from './index';
import getTimestamp from './timeTool';
import playerInit, { getVideoTime, getVideoID, pauseVideo } from './player';
import userObj from './auth';
import setSnackBar from './snackbar';

import '../css/editor.css';

const editorID = 'hello';

const addQuickNote = (quickData) => {
	quickRef.push(quickData);
};

const addVideoNote = (vid, videoData) => {
	videoRef.child(vid).update({ update: getTimestamp() });
	videoRef.child(`${vid}/notes`).push(videoData);
};

const insertText = (target, text) => {
	const textbox = $(target);
	let textStr = textbox.val();
	const startPos = textbox[0].selectionStart;
	const endPos = textbox[0].selectionEnd;
	const middleLength = endPos - startPos;

	const beforeStr = textStr.substr(0, startPos);
	const middleStr = textStr.substr(startPos, middleLength);
	const afterStr = textStr.substr(endPos, textStr.length);
	const inserTxt = middleLength !== 0 ? text.replace('_', middleStr) : text;

	textStr = `${beforeStr}${inserTxt}${afterStr}`;
	textbox.val(textStr);
	const focusPos = `${beforeStr}${inserTxt}`.length;

	textbox[0].setSelectionRange(focusPos, focusPos);
	textbox.focus();
	return textStr;
};

export const setQuickNoteEditor = () => {
	$('.add-btn').on('click', () => {
		if (userObj.user) {
			const jp = $('#t-jp').val();
			const tw = $('#t-zh').val();
			const note = $('#t-note').val();
			addQuickNote({ note, tw, jp });

			$('.t-area').val('');
			getQuickNote();
		} else {
			setSnackBar('');
		}
	});
};

export const setVideoNoteEditor = () => {
	$('.add-btn').on('click', () => {
		pauseVideo();

		if (userObj.user) {
			const jp = $('#t-jp').val();
			const zh = $('#t-zh').val();
			const time = getVideoTime().toString();
			const vid = getVideoID();
			addVideoNote(vid, { time, zh, jp });

			$('.t-area').val('');
			getVideoNote();
		} else {
			setSnackBar('');
		}
	});

	$('.quick-buttons .mark').on('click', function () {
		const thisTxt = $(this).attr('rel');
		insertText($('#t-jp'), thisTxt);
	});
};

$(document).ready(() => {
	if (isVideoNote()) {
		$('.editor').addClass('video');
		playerInit();
	} else {
		$('.editor').addClass('quick');
	}
});

export default editorID;



